.reference-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  text-align: left;

  @media (min-width: 768px) {
    display: flex;
    flex-wrap: wrap;
    width: auto;
    margin: auto;
    justify-content: space-between;
    margin-left: 37px;
    margin-right: 36px;
    padding: 0;
  }

  @media (min-width: 1200px) {
    display: flex;
    flex-wrap: wrap;
    width: auto;
    margin: auto;
    justify-content: space-between;
    margin-left: 53px;
    margin-right: 53px;
    padding-top: 20px;
    padding-bottom: 18px;
  }
}

.reference-list--sights {
  @media (min-width: 768px) {
    padding: 0;
    margin-left: 84px;
    margin-right: 84px;
    padding-top: 1px;
  }

  @media (min-width: 1200px) {
    margin-left: 44px;
    margin-right: 44px;
    padding-top: 33px;
    padding-bottom: 33px;
  }
}

.reference-list__item {
  margin: 0;
  padding: 0;
  display: block;
  margin-bottom: 16px;
  position: relative;

  @media (min-width: 768px) {
    width: 280px;
  }

  @media (min-width: 1200px) {
    margin: 0;
    padding: 0;
    width: auto;
  }
}

.reference-list__item:last-child {
  margin: 0;
}

.reference-list__item--sight {
  @media (min-width: 768px) {
    width: 224px;
  }

  @media (min-width: 1200px) {
    width: auto;
  }
}

.reference-list__label-item {
  text-transform: none;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: #000000;
  margin: 0;
  padding: 0;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 62px;
  position: relative;
  display: block;
}

.reference-list__control-mark {
  position: absolute;
  width: 46px;
  height: 46px;
  background-color: #f2f2f2;
  top: 0;
  border-radius: 50%;
}

.reference-list__input-item[type="checkbox"]:checked + .reference-list__control-mark::before {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  content: "";
  background-image: url("../img/form/tick.svg");
  background-repeat: no-repeat;
  width: 22px;
  height: 16px;
  top: 15px;
}


.reference-list__input-item[type="checkbox"] + .reference-list__control-mark {
  border-radius: 0;
}

.reference-list__input-item[type="radio"]:checked + .reference-list__control-mark::before {
  position: absolute;
  top: 15px;
  left: 15px;
  width: 16px;
  height: 16px;
  background-color: #333333;
  border-radius: 50%;
  content: "";
}

.reference-list__item:hover .reference-list__control-mark {
  background-color: #e5e5e5;
}

.reference-list__item:active .reference-list__control-mark {
  background-color: #d5d5d5;
  opacity: 0.3;
}
